import React, { useState, useEffect } from "react";
import styles from "./GameHistory.module.css";
import HistoryElement from "./HistoryElement/HistoryElement";
import { db, auth } from "../../firebase";
import { useList } from "react-firebase-hooks/database";
import { useAuthState } from "react-firebase-hooks/auth";
import firebase from "firebase";

function GameHistory() {
  const [user, userLoading] = useAuthState(auth);
  var dbRef = db.ref("/" + user.uid);
  const dbQuery = dbRef.orderByChild("timestamp");

  const [snapshots, loading, error] = useList(dbQuery);
  const [delGame, setDelete] = useState("potato");
  const [reloadHistory, setreloadHistory] = useState("x");

  return (
    <div className={styles.gameHistory}>
      <div className={styles.topSectionHistory}>
        {!loading && snapshots && reloadHistory === "x"
          ? setreloadHistory(snapshots.length)
          : ""}

        {!loading &&
        snapshots &&
        reloadHistory !== "x" &&
        snapshots.length === reloadHistory - 1
          ? setreloadHistory(snapshots.length)
          : ""}

        {!loading &&
        snapshots &&
        reloadHistory !== "x" &&
        snapshots.length !== reloadHistory &&
        snapshots.length !== reloadHistory - 1
          ? window.location.reload()
          : ""}
        {console.log(reloadHistory)}
        {!loading && snapshots ? (
          snapshots
            .slice(0)
            .reverse()
            .map((game, index) => (
              <div
                onClick={() => {
                  if (delGame === game.key) {
                    setDelete("potato");
                  } else {
                    setDelete(game.key);
                  }
                }}
                key={index}
              >
                <HistoryElement
                  isUser={game.val().isUser}
                  name={game.val().name}
                  date={game.val().date}
                  color={game.val().color}
                  isClicked={delGame === game.key}
                />
              </div>
            ))
        ) : (
          <div>
            <img src="https://upload.wikimedia.org/wikipedia/commons/7/7d/Pedro_luis_romani_ruiz.gif" />
          </div>
        )}
      </div>
      <div
        className={styles.gradientButton}
        onClick={() => dbRef.child(delGame).remove()}
      >
        <h1>Deleted Selected</h1>
      </div>
    </div>
  );
}

export default GameHistory;
